
import { defineComponent } from "vue";
import store from "@/store";
import CurrentPathBar from "@/components/CurrentPathBar.vue";
import { PageInfo } from "@/myInterface";

export default defineComponent({
  name: "AccountCenter",
  store: store,
  components: { CurrentPathBar },
  data() {
    return {
      publicPath:
        process.env.NODE_ENV === "production" ? "/my_online_shop/" : "/",
      parentPageList: [
        {
          name: "Home",
          path: "",
        },
      ] as PageInfo[],
    };
  },
  computed: {
    userName(): string {
      return store.state.userInfo.name;
    },
    fullPathList(): PageInfo[] {
      return this.parentPageList.concat([
        {
          name: "Account Center",
          path: "#",
        },
      ]);
    },
  },
  methods: {
    async logout(): Promise<void> {
      await store.dispatch("logout");
      window.location.replace(this.publicPath);
    },
  },
});
